import React from "react";
import {useState, useEffect} from "react";
import "./App.css";
import mondaySdk from "monday-sdk-js";
import "monday-ui-react-core/dist/main.css";
//Explore more Monday React Components here: https://style.monday.com/
import AttentionBox from "monday-ui-react-core/dist/AttentionBox.js";

import * as d3 from "d3";

// Usage of mondaySDK example, for more information visit here: https://developer.monday.com/apps/docs/introduction-to-the-sdk/
const monday = mondaySdk();

function TimeLinePlot() {
    // set the dimensions and margins of the graph
    const margin = {top: 10, right: 100, bottom: 30, left: 30},
        width = 460 - margin.left - margin.right,
        height = 400 - margin.top - margin.bottom;

// append the svg object to the body of the page
    const svg = d3.select("#my_dataviz")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", `translate(${margin.left},${margin.top})`);

//Read the data
    d3.csv().then(function (data) {

        // List of groups (here I have one group per column)
        const allGroup = ["valueA", "valueB", "valueC"]

        /*// Reformat the data: we need an array of arrays of {x, y} tuples
        const dataReady = allGroup.map( function(grpName) { // .map allows to do something for each element of the list
            return {
                name: grpName,
                values: data.map(function(d) {
                    return {time: d.time, value: +d[grpName]};
                })
            };
        });
        // I strongly advise to have a look to dataReady with
        console.log(dataReady)*/

        const dataReady = [
            {
                "name": "valueA",
                "values": [
                    {
                        "time": "1",
                        "value": 2
                    },
                    {
                        "time": "2",
                        "value": 3
                    },
                    {
                        "time": "3",
                        "value": 1
                    },
                    {
                        "time": "4",
                        "value": 7
                    },
                    {
                        "time": "5",
                        "value": 8
                    },
                    {
                        "time": "6",
                        "value": 8
                    },
                    {
                        "time": "7",
                        "value": 5
                    },
                    {
                        "time": "8",
                        "value": 4
                    },
                    {
                        "time": "9",
                        "value": 9
                    },
                    {
                        "time": "10",
                        "value": 11
                    }
                ]
            },
            {
                "name": "valueB",
                "values": [
                    {
                        "time": "1",
                        "value": 5
                    },
                    {
                        "time": "2",
                        "value": 4
                    },
                    {
                        "time": "3",
                        "value": 4
                    },
                    {
                        "time": "4",
                        "value": 4
                    },
                    {
                        "time": "5",
                        "value": 8
                    },
                    {
                        "time": "6",
                        "value": 13
                    },
                    {
                        "time": "7",
                        "value": 15
                    },
                    {
                        "time": "8",
                        "value": 17
                    },
                    {
                        "time": "9",
                        "value": 18
                    },
                    {
                        "time": "10",
                        "value": 13
                    }
                ]
            },
            {
                "name": "valueC",
                "values": [
                    {
                        "time": "1",
                        "value": 13
                    },
                    {
                        "time": "2",
                        "value": 14
                    },
                    {
                        "time": "3",
                        "value": 16
                    },
                    {
                        "time": "4",
                        "value": 12
                    },
                    {
                        "time": "5",
                        "value": 7
                    },
                    {
                        "time": "6",
                        "value": 9
                    },
                    {
                        "time": "7",
                        "value": 3
                    },
                    {
                        "time": "8",
                        "value": 2
                    },
                    {
                        "time": "9",
                        "value": 1
                    },
                    {
                        "time": "10",
                        "value": 1
                    }
                ]
            }
        ];

        // A color scale: one color for each group
        const myColor = d3.scaleOrdinal()
            .domain(allGroup)
            .range(d3.schemeSet2);

        // Add X axis --> it is a date format
        const x = d3.scaleLinear()
            .domain([0, 10])
            .range([0, width]);
        svg.append("g")
            .attr("transform", `translate(0, ${height})`)
            .call(d3.axisBottom(x));

        // Add Y axis
        const y = d3.scaleLinear()
            .domain([0, 20])
            .range([height, 0]);
        svg.append("g")
            .call(d3.axisLeft(y));

        // Add the lines
        const line = d3.line()
            .x(d => x(+d.time))
            .y(d => y(+d.value))
        svg.selectAll("myLines")
            .data(dataReady)
            .join("path")
            .attr("d", d => line(d.values))
            .attr("stroke", d => myColor(d.name))
            .style("stroke-width", 4)
            .style("fill", "none")

        // Add the points
        svg
            // First we need to enter in a group
            .selectAll("myDots")
            .data(dataReady)
            .join('g')
            .style("fill", d => myColor(d.name))
            // Second we need to enter in the 'values' part of this group
            .selectAll("myPoints")
            .data(d => d.values)
            .join("circle")
            .attr("cx", d => x(d.time))
            .attr("cy", d => y(d.value))
            .attr("r", 5)
            .attr("stroke", "white")

        // Add a legend at the end of each line
        svg
            .selectAll("myLabels")
            .data(dataReady)
            .join('g')
            .append("text")
            .datum(d => {
                return {name: d.name, value: d.values[d.values.length - 1]};
            }) // keep only the last value of each time series
            .attr("transform", d => `translate(${x(d.value.time)},${y(d.value.value)})`) // Put the text at the position of the last point
            .attr("x", 12) // shift the text a bit more right
            .text(d => d.name)
            .style("fill", d => myColor(d.name))
            .style("font-size", 15)

    })
    return (
        <div id="my_dataviz"></div>
    );
}

const App = () => {
    const [context, setContext] = useState();

    useEffect(() => {
        // Notice this method notifies the monday platform that user gains a first value in an app.
        // Read more about it here: https://developer.monday.com/apps/docs/mondayexecute#value-created-for-user/
        monday.execute("valueCreatedForUser");

        // TODO: set up event listeners, Here`s an example, read more here: https://developer.monday.com/apps/docs/mondaylisten/
        monday.listen("context", (res) => {
            setContext(res.data);
        });
    }, []);

    //Some example what you can do with context, read more here: https://developer.monday.com/apps/docs/mondayget#requesting-context-and-settings-data
    const attentionBoxText = `Hello, your user_id is: ${
        context ? context.user.id : "still loading"
    }.
  This will show a timeline`;

    return (
        <div className="App">
            <h1>Timeline</h1>
            <br/>
            <TimeLinePlot/>
        </div>
    );
};

export default App;
